import { Amenity, BookingData } from './Booking.types';

import InternetSVG from '#icons/internet-solid-24.svg';
import LocalParkingSVG from '#icons/local-parking-solid-24.svg';
import RestaurantSVG from '#icons/restaurant-solid-24.svg';
import RoomServiceSVG from '#icons/room-service-solid-24.svg';
import SpaSVG from '#icons/spa-solid-24.svg';

export const readReviewButtonLabel = (name: string) =>
  `Read our full review of ${name} here`;

export const iconMap: { [key: string]: ReactNode } = {
  internet: InternetSVG,
  services: RoomServiceSVG,
  foodDrink: RestaurantSVG,
  parking: LocalParkingSVG,
  health: SpaSVG,
};

export class AmenityBuilder {
  amenity = {
    ref: '',
    name: '',
    items: [],
    showTooltip: false,
  } as Amenity;

  name(name: string) {
    this.amenity.name = name;

    return this;
  }

  items(items: string[]) {
    this.amenity.items = items;

    return this;
  }

  showTooltip(showTooltip: boolean) {
    this.amenity.showTooltip = showTooltip;

    return this;
  }

  ref(ref: string) {
    this.amenity.ref = ref;

    return this;
  }

  build() {
    return this.amenity;
  }
}

export class BookingBuilder {
  booking = {
    id: '',
    name: '',
    price: '',
    reviewUrl: '',
    isExternalUrl: false,
    url: '',
    amenities: [],
  } as BookingData;

  id(id: string) {
    this.booking.id = id;

    return this;
  }

  name(name: string) {
    this.booking.name = name;

    return this;
  }

  url(url: string) {
    this.booking.url = url;

    return this;
  }

  price(price: string) {
    this.booking.price = price;

    return this;
  }

  reviewUrl(reviewUrl: string) {
    this.booking.reviewUrl = reviewUrl;

    return this;
  }

  isExternalUrl(isExternalUrl: boolean) {
    this.booking.isExternalUrl = isExternalUrl;

    return this;
  }

  amenities(amenities: Amenity[]) {
    this.booking.amenities = amenities;

    return this;
  }

  build() {
    return this.booking;
  }
}
