import { FC, useState } from 'react';

import Button from '#components/Button';
import { BUTTON_SIZE_LARGE } from '#components/Button/Button.constants';
import Dropdown from '#components/Dropdown/Dropdown';
import Icon from '#components/Icon';
import IconButton from '#components/IconButton';
import { PopupTip } from '#components/PopupTip';

import {
  AMENITIES_LABEL,
  COST_SUFFIX_LABEL,
  CTA_LABEL,
  DISCLAIMER_LABEL,
  PRICE_AVAILABILITY_LABEL,
  ROOM_AVAILABILITY_LABEL,
} from './Booking.constants';
import { iconMap, readReviewButtonLabel } from './Booking.helpers';
import {
  AccordionBody,
  AccordionHeader,
  AmenitiesList,
  Amenity,
  AmenityGroup,
  AmenityGroupHeader,
  AmenityList,
  CostPrefix,
  CostPrice,
  CostSection,
  CostSuffix,
  CostUnknown,
  Disclaimer,
  MainSection,
  ReadMoreButton,
  ReadMoreSection,
  RotateEl,
  Wrapper,
} from './Booking.styles';
import { BookingProps } from './Booking.types';

import ChevronDownSVG from '#icons/chevron-down-solid-24.svg';
import InternetSVG from '#icons/internet-solid-24.svg';

const TOOLTIP_TEXT = 'Please check hotel for more information on amenities';

const Booking: FC<BookingProps> = ({
  id,
  name,
  url,
  isExternalUrl,
  reviewUrl,
  price,
  amenities,
}) => {
  const [isOpen, setIsOpen] = useState(false);

  function onDropdownInteract() {
    setIsOpen((isOpen) => !isOpen);
  }

  return (
    <Wrapper>
      <MainSection>
        <CostSection>
          <CostPrefix>{ROOM_AVAILABILITY_LABEL}</CostPrefix>
          {price && (
            <>
              <CostPrice data-testid="price">{price}</CostPrice>
              <CostSuffix>{COST_SUFFIX_LABEL}</CostSuffix>
            </>
          )}
          {!price && (
            <CostUnknown data-testid="price-availability">
              {PRICE_AVAILABILITY_LABEL}
            </CostUnknown>
          )}
        </CostSection>
        <div>
          <Button
            as="a"
            target="_blank"
            rel="noreferrer"
            size={BUTTON_SIZE_LARGE}
            variant="primary"
            href={url}
          >
            {CTA_LABEL}
          </Button>
          <Disclaimer>{DISCLAIMER_LABEL}</Disclaimer>
        </div>
      </MainSection>

      {reviewUrl && (
        <ReadMoreSection>
          <ReadMoreButton
            data-testid="read-more-button"
            forwardedAs="a"
            href={reviewUrl}
            variant="tertiary"
            size="small"
            {...(isExternalUrl && {
              target: '_blank',
              rel: 'noreferrer',
            })}
          >
            {readReviewButtonLabel(name)}
          </ReadMoreButton>
        </ReadMoreSection>
      )}

      <Dropdown
        id={id}
        isOpen={isOpen}
        onInteract={onDropdownInteract}
        trigger={
          <AccordionHeader>
            {AMENITIES_LABEL}
            <RotateEl $isOpen={isOpen}>
              <IconButton
                isCosy={true}
                svg={ChevronDownSVG}
                isDisabled={false}
              />
            </RotateEl>
          </AccordionHeader>
        }
      >
        <AccordionBody>
          <AmenitiesList>
            {amenities.map((amenity) => {
              return (
                <AmenityGroup key={amenity.name}>
                  <AmenityGroupHeader>
                    <Icon svg={iconMap[amenity.ref] || InternetSVG} />
                    {amenity.name}
                    {amenity.showTooltip && (
                      <PopupTip size="base">{TOOLTIP_TEXT}</PopupTip>
                    )}
                  </AmenityGroupHeader>
                  <AmenityList>
                    {amenity.items.map((item) => (
                      <Amenity key={item}>{item}</Amenity>
                    ))}
                  </AmenityList>
                </AmenityGroup>
              );
            })}
          </AmenitiesList>
        </AccordionBody>
      </Dropdown>
    </Wrapper>
  );
};

export default Booking;
